<template>
  <div>
    <section id="top">
      <div>
        <p>到期未收账单</p>
        <p>{{sumN}}</p>
      </div>
      <p class="line"></p>
      <div>
        <p>账单总金额（元）</p>
        <p>&yen;&ensp;{{sumP?sumP.toFixed(2):sumP}}</p>
      </div>
    </section>
    <div id="top_box">
      <section
        id="center"
        v-for="(room) in roomPay"
        :key="room.id"
        @click="goRoom(room)"
      >
        <div>
          <div class="c_top">
            <div>

              <p>
                {{room.startTime.substr(0,10)}}
                <span
                  class="box"
                  id="rent"
                >租金</span>
              </p>
              <p v-if="3 > room.diff && room.diff > 0">还有{{Math.abs(room.diff)}}天到期</p>
              <p v-else-if="room.diff < 0">已逾期{{Math.abs(room.diff)}}天</p>
              <p v-else-if="room.diff == 0">今天到期</p>
            </div>
            <p>&yen;{{room.roomPrice?room.roomPrice:0}}</p>
          </div>
          <p v-if="room.roomInfo || room.cooperName">{{ room.roomInfo | splitRoomSn }}&ensp;{{ room.cooperName }}</p>
        </div>
      </section>
      <section
        id="center"
        v-for="(life) in lifePay"
        :key="life.id"
        @click="goLife(life)"
      >
        <div>
          <div class="c_top">
            <div>
              <p>
                {{life.endTime}}
                <span
                  class="box"
                  id="ele"
                >水费</span>
              </p>
              <p v-if="life.diff < 0">已逾期{{Math.abs(life.diff)}}天</p>
              <p v-else-if="life.diff < 3 && life.diff > 0">还有{{Math.abs(life.diff)}}天到期</p>
              <p v-else-if="life.diff == 0">今天到期</p>
            </div>
            <p>&yen;{{life.totalPrice?life.totalPrice:0}}</p>
          </div>
          <p v-if="life.roomInfo || life.cooperName">{{ life.roomInfo | splitRoomSn }}&ensp;{{ life.cooperName }}</p>
        </div>
      </section>
    </div>
  </div>
</template>
<script>
import { GetBusinessRentalBillUnpayByhotel, GetBusinessLifeCostBillUnpayByhotel } from '@/api/api_business_hotel_and_scale';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            roomPay: [],
            lifePay: [],
            sumP: '',
            sumN: ''
        };
    },
    created() {
        this.$store.commit('setHeader', '未收账单');
        this.hotelId = this.$route.query.hotelId;
        this.getRoomUnpay();
    },
    methods: {
        // 根据酒店查询所有未支付的房费账单
        getRoomUnpay() {
            GetBusinessRentalBillUnpayByhotel(this.hotelId).done((res) => {
                this.roomPay = res.data;
                this.roomNum = res.data.length;
                let array = res.data;
                this.roomP = array.reduce(function (total, cur, idx, arr) {
                    return total + cur.roomPrice;
                }, 0);
                for (let i = 0; i < array.length; i++) {
                    array[i].diff = this.getTime(array[i].startTime.substr(0, 10));
                }
                this.roomPay.sort(function (a, b) {
                    return a.diff - b.diff;
                });
                this.getLifeUnpay();
            });
        },
        // 查询酒店查询所有未支付未提交的生活账单
        getLifeUnpay() {
            GetBusinessLifeCostBillUnpayByhotel(this.hotelId).done((res) => {
                this.lifePay = res.data;
                this.lifeNum = res.data.length;
                let array = res.data;
                this.lifeP = array.reduce(function (total, cur, idx, arr) {
                    return total + cur.totalPrice;
                }, 0);
                this.sumP = this.roomP + this.lifeP;
                this.sumN = this.roomNum + this.lifeNum;
                for (let i = 0; i < array.length; i++) {
                    array[i].diff = this.getTime(array[i].endTime);
                }
                this.lifePay.sort(function (a, b) {
                    return a.diff - b.diff;
                });
            });
        },
        // 相差天数
        getTime(time) {
            const days = dayjs();
            let nowTime = days.format('YYYY-MM-DD');
            let diff = dayjs(time).diff(dayjs(nowTime), 'day');
            return diff;
        },
        // 跳到房费页面
        goRoom(room) {
            this.$router.push({
                path: '/rentInfo_long',
                query: {
                    orderRoomId: room.orderRoomId
                }
            });
        },
        // 跳到水电页面
        goLife(life) {
            this.$router.push({
                path: '/rentInfo_long',
                query: {
                    orderRoomId: life.orderRoomId
                }
            });
        }
    }
};
</script>
<style scoped>
#top {
    display: flex;
    justify-content: space-around;
    height: 100px;
    background: #2e7bf7;
    color: #fff;
    align-items: center;
    font-size: 0.8rem;
}
#top > .line {
    height: 40%;
    width: 1px;
    background: #fff;
}
#top > div > p:nth-child(2) {
    font-size: 1.2rem;
    padding-top: 6px;
    font-weight: bold;
}
#top_box {
    font-size: 0.8rem;
    position: fixed;
    top: 156px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    overflow-y: scroll;
}
#center {
    font-size: 0.8rem;
    margin: 14px 10px;
    background: #fff;
    text-align: left;
    color: #000;
    border-radius: 5px;
}
#center > div > .c_top {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 6px;
    border-bottom: 1px solid #e5e5e5;
    line-height: 26px;
}
#center > div > .c_top > div {
    width: 50%;
}
#center > div > .c_top > div > p:nth-child(2) {
    color: #ff4848;
    font-size: 0.7rem;
}
#center > div > p {
    padding-left: 10px;
    color: #888;
    padding: 4px 6px;
    font-size: 0.6rem;
}
.box {
    display: inline-block;
    width: 26%;
    height: 16px;
    background: #ff7d41;
    line-height: 16px;
    border-radius: 5px;
    font-size: 0.6rem;
    text-align: center;
    color: #fff;
    margin-left: 6px;
}
#rent {
    background: #ff7d41;
}
#ele {
    background: #02bc6c;
}
</style>



